body {
  background: #e2e9ef;
  font-size: 14px;
  font-family: Arial,sans-serif;
  color: #304760;
}

.tabletop {
  background: #70e07c;
  color: #fff;
  display: flex;
  align-items: baseline;
  flex-direction: row;
  justify-content: space-between;
  height: 70px;
}

.sortarrow {
  display: flex;
  justify-content: center;
	align-items: center;
  transition: transform .5s cubic-bezier(.075,.82,.165,1);
  margin-left: 5px;
}

.project-title {
  padding-left: 20px;
}

.searchbox {
  padding-right: 20px;
}

.searchfield {
  border-radius: 13px;
  padding: 5px 10px;
  border: none;
  outline: none;
  width: 27px;
  transition: all .3s cubic-bezier(.165,.84,.44,1);
}

.searchfield:focus {  
  width: 160px;
  transition: all .3s cubic-bezier(.165,.84,.44,1);
}

.table {
  background: #f8f9fb;
  display: flex;
  flex-direction: column;
  overflow-x: auto;
  top: 70px;
  left: 0;
  right: 0;
  bottom: 0;
  position: absolute;
}

.thead {
  background: #e2e9ef;
  position: sticky;
  top: 0;
  font-weight: bold;
}

.th:hover svg path {
  stroke: #304760;
  fill: #304760;
}

.rotate180 {
  transform: rotate(180deg);
}

.tbody > .tr:hover {
  background-color: rgba(66,180,230,.2);
}

.tr {
  display: grid;
  border-bottom: 1px solid white;
  align-items: center;
  cursor: pointer;    
  padding: 4px 24px 4px 24px;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
}

.th {
  display: flex;
}

.td {
  margin-right: 4px;
}

@media screen and (max-width: 820px) {
  .thead .th:nth-child(5) {
    display: none;
  }

  .tbody .td:nth-child(5) {
    display: none;
  }
}

@media screen and (max-width: 670px) {
  .thead .th:nth-child(4) {
    display: none;
  }

  .tbody .td:nth-child(4) {
    display: none;
  }
}

@media screen and (max-width: 520px) {
  .thead .th:nth-child(3) {
    display: none;
  }

  .tbody .td:nth-child(3) {
    display: none;
  }
}

@media screen and (max-width: 370px) {
  .thead .th:nth-child(2) {
    display: none;
  }

  .tbody .td:nth-child(2) {
    display: none;
  }
}